import './AdminFabricanteDetail.css'

import { IonAlert, IonContent, IonHeader, IonModal, IonSpinner, IonTitle, IonToolbar } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'

import { AdminItemDetail, LoaderAdmin, Page, SDataTable } from '../../components'
import ComponentCalificaciones from '../../components/ComponenteCalificaciones'
import { RUTAS } from '../../constants'
import { protectedRef, useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Calificacion } from '../../services/api'

const moment = require('moment-timezone')

type AdminFabricanteDetailProps = {
  history: any
  match: any
}

export const AdminFabricanteDetail: React.FC<AdminFabricanteDetailProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { adminStore, loginStore } = rootStore
  const [Fabricante, setFabricante] = useState(null as any)
  const [Calificaciones, setCalificaciones] = useState<Calificacion[]>([])
  const [showAlert, setShowAlert] = useState(false)
  const [showUserModal, setShowUserModal] = useState(false)
  const [Loading, setLoading] = useState(true)
  const [ClienteEliminar, setClienteEliminar] = useState(null)
  const DataTable = React.createRef<any>()
  const [userVerificado, setUserVerificado] = useState(false)
  const [userError, setUserError] = useState('')
  const [EmailUser, setEmailUser] = useState('')
  const Estado = match.params.estado
  const idFabricante = match.params.id
  const apiToaster = useApiToaster()

  const setup = async () => {
    if (loginStore.fabricante_id) {
      obtenerFabricante(loginStore.fabricante_id)
    } else if (!idFabricante) {
      document.location.href = '/admin'
    } else {
      obtenerFabricante(idFabricante)
      obtenerCalicaciones(idFabricante)
    }
  }

  useEffect(() => {
    setup()
  }, [idFabricante])

  const obtenerCalicaciones = (fabricante_id: number) => {
    setLoading(true)
    rootStore.environment.api.calificacionesList(fabricante_id, 'fabricante').then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setCalificaciones(result.data)
      }
    })
  }

  const obtenerFabricante = (fabricante_id: number) => {
    rootStore.environment.api.getFabricante(fabricante_id).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setFabricante(result.fabricante)
      }
    })
  }

  const aprobarFabricante = () => {
    if (!Fabricante || Fabricante.aprobado || !loginStore.administrador) {
      return
    }
    rootStore.environment.api.aprobarFabricante(Fabricante.id).then((result: any) => {
      apiToaster(result, 'El fabricante se aprobó exitosamente!', 'aprobar el fabricante')
      if (result.kind === 'ok') {
        obtenerFabricante(Fabricante.id)
      }
    })
  }

  useEffect(() => {
    if (DataTable.current) {
      // @ts-ignore
      DataTable.current.refresh()
    }
  }, [Fabricante])

  const editar = (row: any) => {
    history.push(RUTAS.ADMIN_FABRICANTE_UPDATE(Estado, row.id))
  }

  const editarPuntoRetiro = (row: any) => {
    adminStore.setPuntoRetiroDetalle(row)
    history.push(RUTAS.ADMIN_FABRICANTE_PUNTO_RETIRO_UPDATE(Estado, Fabricante.id, row.id))
  }

  const agregarPuntoRetiro = () => {
    adminStore.setPuntoRetiroDetalle(null)
    history.push(RUTAS.ADMIN_FABRICANTE_PUNTO_RETIRO_CREATE(Estado, Fabricante.id))
  }

  const eliminar = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    rootStore.environment.api.eliminarFabricante(row.id).then((result: any) => {
      apiToaster(result, 'Eliminado satisfactoriamente', 'eliminar')
      if (result.kind === 'ok') {
        history.push(RUTAS.ADMIN_FABRICANTES(Estado))
      }
    })
  }

  const visualizar = (row: any) => {
    adminStore.setClienteDetalle(row)
    // @ts-ignore
    history.push({
      pathname: '/admin/staff/detail',
      origen: 'fabricante',
    })
  }

  const handleEditar = (row: any) => {
    adminStore.setClienteDetalle(row)
    // @ts-ignore
    history.push({
      pathname: '/admin/staff/update',
      origen: 'fabricante',
      // @ts-ignore
      fabricante_id: idFabricante,
    })
  }

  const handleEliminar = (row: any) => {
    setShowAlert(true)
    setClienteEliminar(row.id)
  }

  const eliminarCliente = () => {
    const dtable = DataTable.current
    rootStore.environment.api.eliminarUsuarioStaff(ClienteEliminar, idFabricante).then((result: any) => {
      apiToaster(result, 'Usuario eliminado exitosamente', 'eliminar')
      dtable.refresh()
    })
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}>
          <FIcon.Eye size={14} />
        </span>
        <span
          className={'button ml-3'}
          onClick={() => {
            handleEditar(row)
          }}
        >
          <FIcon.Edit size={14} />
        </span>
        {loginStore.administrador && (
          <span
            className={'button ml-3'}
            onClick={() => {
              handleEliminar(row)
            }}
          >
            <FIcon.Trash size={14} />
          </span>
        )}
      </div>
    )
  }

  const verificarUsuario = () => {
    setUserVerificado(false)
    setLoading(true)
    rootStore.environment.api.verificarUsuario(EmailUser).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setUserVerificado(true)
        setUserError('')
      } else if (result.kind === 'bad-data') {
        setUserVerificado(false)
        setUserError(result.errors[0])
      } else {
        setUserVerificado(false)
        apiToaster(result, '', 'verificar usuario')
      }
    })
  }

  const handleAgregarUser = () => {
    setLoading(true)
    const dtable = DataTable.current
    rootStore.environment.api.agregarUsuarioAFabricante(idFabricante, EmailUser).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setShowUserModal(false)
        apiToaster(result, 'Se agregó el usuario exitosamente!')
        dtable.refresh()
      } else if (result.kind === 'bad-data') {
        setUserVerificado(false)
        setUserError(result.errors[0])
      } else {
        setUserVerificado(false)
        apiToaster(result, '', 'agregar usuario')
      }
    })
  }

  const eliminarPuntoRetiro = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    rootStore.environment.api.eliminarPuntoRetiro(row.id).then((result: any) => {
      if (!Fabricante) {
        return
      }
      if (result.kind === 'ok') {
        apiToaster(result, 'Eliminado satisfactoriamente', '')
        // @ts-ignore
        const puntosRetiro = [...Fabricante.puntos_retiro]
        const puntoRet = puntosRetiro.find((pret) => pret.id === row.id)
        if (puntoRet && adminStore.fabricanteDetalle) {
          puntosRetiro.splice(puntosRetiro.indexOf(puntoRet), 1)
          adminStore.fabricanteDetalle.setPuntosRetiro(puntosRetiro)
        }
      } else {
        apiToaster(result, '', 'eliminar')
      }
      if (loginStore.fabricante_id) {
        obtenerFabricante(loginStore.fabricante_id)
      } else {
        if (idFabricante) {
          obtenerFabricante(idFabricante)
        }
      }
    })
  }
  return (
    <Page login_required admin>
      <IonAlert
        isOpen={showAlert}
        onDidDismiss={() => setShowAlert(false)}
        header={'Eliminar usuario'}
        message={'Confirma que desea eliminar el usuario?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'btn-secondary',
          },
          {
            text: 'Eliminar',
            cssClass: 'btn-primary',
            handler: () => {
              eliminarCliente()
            },
          },
        ]}
      />
      <IonModal isOpen={showUserModal} backdropDismiss={false}>
        <IonHeader>
          <IonToolbar>
            <IonTitle>Buscar usuario a agregar</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <div>
            <div className={'m-4'}>
              <label className={'font-weight-bold text-primary'} id={'email-user'}>
                Correo del Usuario
              </label>
              <input
                type={'email'}
                className={'form-control'}
                id={'email-user'}
                value={EmailUser}
                onChange={(e) => {
                  setEmailUser(e.target.value)
                  setUserVerificado(false)
                  setUserError('')
                }}
              />
              <div className={'verificar-user-div-container mt-3'}>
                <button className={'btn btn-secondary btn-block boton-buscar-usuario'} onClick={verificarUsuario}>
                  Buscar usuario
                </button>
                <div>
                  {!Loading && userVerificado && userError == '' && (
                    <div className={'icono-globo-admin-user'} style={{ background: 'green' }}>
                      <FIcon.Check size={18} style={{ color: 'white' }} />
                    </div>
                  )}
                  {userError !== '' && (
                    <div className={'icono-globo-admin-user'} style={{ background: 'red' }}>
                      <FIcon.X size={18} style={{ color: 'white' }} />
                    </div>
                  )}
                  {Loading && (
                    <div className={'icono-globo-admin-user'} style={{ background: '#303AB2' }}>
                      <IonSpinner name="crescent" />
                    </div>
                  )}
                </div>
              </div>
              <div className={'verificar-user-div-container-error'}>
                {userError !== '' && <span className={'error-msg'}>{userError}</span>}
              </div>
            </div>
          </div>
        </IonContent>
        <div style={{ padding: 10 }}>
          <div className={'row'}>
            <div className={'col-12 col-md-6 mb-xs-2'}>
              <button className={'btn btn-secondary btn-block'} onClick={() => setShowUserModal(false)}>
                Cancelar
              </button>
            </div>

            <div className={'col-12 col-md-6'}>
              <button
                className={'btn btn-primary btn-block'}
                disabled={!userVerificado || userError !== ''}
                onClick={() => {
                  handleAgregarUser()
                }}
              >
                Agregar
              </button>
            </div>
          </div>
        </div>
      </IonModal>

      <LoaderAdmin visible={Loading} />
      {Fabricante && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">{Fabricante.nombre}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  {loginStore.administrador && (
                    <li className="breadcrumb-item">
                      <a onClick={() => history.push('/admin/fabricante/' + Estado)}>Fabricantes</a>
                    </li>
                  )}
                  <li className="breadcrumb-item active">{Fabricante.nombre}</li>
                </ol>
              </div>
            </div>
            <div className={'mb-2 text-right'}>
              {loginStore.administrador && Fabricante && !Fabricante.aprobado && (
                <button className={'btn btn-primary ml-3'} title={'Aprobar'} onClick={aprobarFabricante}>
                  <FIcon.Check size={14} /> Aprobar
                </button>
              )}
              <button className={'btn btn-primary ml-3'} title={'Editar'} onClick={() => editar(Fabricante)}>
                <FIcon.Edit size={14} /> Editar
              </button>
              {loginStore.administrador && (
                <button className={'btn btn-primary ml-3'} title={'Eliminar'} onClick={() => eliminar(Fabricante)}>
                  <FIcon.Trash size={14} /> Eliminar
                </button>
              )}
            </div>
            <div className="card">
              <div className="card-body">
                <div className={'row'}>
                  <AdminItemDetail titulo={'Nombre'} texto={Fabricante.nombre} columnas={3} />
                  <AdminItemDetail titulo={'Razón social'} texto={Fabricante.razon_social} columnas={3} />
                  <AdminItemDetail titulo={'RUC'} texto={Fabricante.ruc} columnas={3} />
                  <AdminItemDetail titulo={'Número de celular'} texto={Fabricante.nro_telefono} columnas={3} />
                  {Fabricante.resena_empresa && (
                    <AdminItemDetail titulo={'Reseña de la empresa'} html={Fabricante.resena_empresa} columnas={12} />
                  )}
                  <AdminItemDetail titulo={'Aprobado'} texto={Fabricante.aprobado ? 'Si' : 'No'} columnas={3} />
                  {loginStore.administrador && (
                    <AdminItemDetail titulo={'Aprobado por'} texto={Fabricante.aprobado_por} columnas={3} />
                  )}
                  <AdminItemDetail titulo={'Calificación'} texto={`${Fabricante.rating}☆ en ${Fabricante.ratings} pedidos`} columnas={3}/>
                  {loginStore.administrador && (
                    <AdminItemDetail
                      titulo={'Agregado el'}
                      texto={moment(Fabricante.fecha_agregado).format('DD/MM/YYYY')}
                      columnas={3}
                    />
                  )}
                  <AdminItemDetail titulo={'Margen'} texto={Fabricante.comision_empresa} columnas={3} />
                  <AdminItemDetail
                    titulo={'Rubros'}
                    texto={Fabricante.rubros && Fabricante.rubros.map((rub: any) => rub.nombre).join(', ')}
                    columnas={12}
                  />
                  <AdminItemDetail titulo={'Logo de la empresa'} imagen={Fabricante.imagen_perfil} columnas={3} />
                </div>
              </div>
            </div>
            <h4>Usuarios </h4>
            <div className="card">
              <div className="card-body">
                <div className={'mb-2 text-right'}>
                  {loginStore.administrador && (
                    <button
                      className={'btn btn-primary '}
                      title={'Agregar usuario'}
                      onClick={() => {
                        adminStore.setClienteDetalle(null)
                        // TODO
                        setShowUserModal(true)
                      }}
                    >
                      <FIcon.Search size={14} /> Agregar usuario existente
                    </button>
                  )}
                  <button
                    className={'btn btn-primary  ml-2'}
                    title={'Crear'}
                    onClick={() => {
                      adminStore.setClienteDetalle(null)
                      // @ts-ignore
                      history.push({
                        pathname: '/admin/staff/update',
                        origen: 'fabricante',
                        fabricante_id: idFabricante,
                      })
                    }}
                  >
                    <FIcon.Plus size={14} /> Crear usuario
                  </button>
                </div>
                <div className="table-responsive">
                  <SDataTable
                    ref={protectedRef(DataTable)}
                    subHeader
                    obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                      // @ts-ignore
                      return rootStore.environment.api.usuariosStaff(page, perPage, order, search, idFabricante)
                    }}
                    columns={[
                      {
                        name: 'Email',
                        selector: 'email',
                        sortable: true,
                      },
                      {
                        name: 'Nombre',
                        selector: 'first_name',
                        sortable: true,
                      },
                      {
                        name: 'Apellido',
                        selector: 'last_name',
                        sortable: true,
                      },
                      {
                        name: 'Acciones',
                        sortable: false,
                        cell: (row: any) => renderAcciones(row),
                      },
                    ]}
                  />
                </div>
              </div>
            </div>
            <h4>Puntos de retiro</h4>
            <div className="card">
              <div className="card-body">
                <div className={'mb-2 text-right'}>
                  <button
                    className={'btn btn-primary'}
                    title={'Agregar punto de retiro'}
                    onClick={() => agregarPuntoRetiro()}
                  >
                    <FIcon.Plus size={14} /> Agregar punto de retiro
                  </button>
                </div>
                <table className={'table'}>
                  <thead>
                    <tr>
                      <th>Nombre</th>
                      <th>Calle Principal</th>
                      <th>Calle Secundaria</th>
                      <th>Número</th>
                      <th>Referencia</th>
                      <th>Predeterminada</th>
                      <th>Acciones</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Fabricante.puntos_retiro &&
                      Fabricante.puntos_retiro.map((pret: any, index: any) => (
                        <tr key={'' + index}>
                          <td>{pret.nombre}</td>
                          <td>{pret.calle_principal}</td>
                          <td>{pret.calle_secundaria}</td>
                          <td>{pret.numero}</td>
                          <td>{pret.referencia}</td>
                          <td>{pret.default ? 'Si' : 'No'}</td>
                          <td>
                            <span className={'button'} title={'Editar'} onClick={() => editarPuntoRetiro(pret)}>
                              <FIcon.Edit size={14} />
                            </span>
                            <span
                              className={'button ml-3'}
                              title={'Eliminar'}
                              onClick={() => eliminarPuntoRetiro(pret)}
                            >
                              <FIcon.Trash size={14} />
                            </span>
                          </td>
                        </tr>
                      ))}
                  </tbody>
                </table>
              </div>
            </div>
            <ComponentCalificaciones titulo={'Proveedor'} calificaciones={Calificaciones} />
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminFabricanteDetail
